.bio-name {
	font-size: $bio-name-font-size	;
	display: none;
  font-family: $font-family;
  font-weight: $heading-font-weight;
  line-height: $heading-line-height;
  margin-top: $space-2;
  margin-bottom: $space-1;
}

.bio-container {
    margin-top: -1em;
	display: flex;
	flex-flow: row wrap;
    justify-content: center;
    text-align: center;
}

.bio-picture {
    max-width:65%;
    position: relative;

    img {
        border-radius: 0.25em;
    }
}

.bio-info {
    min-width:50%;
}

.boxed-primary {
    border-radius: 0.25em;
    padding: 10px;
    background-color: $primary-container;
    p:last-child {
        margin-bottom: 0;
    }
    a,p {
        color:$on-primary-container;
    }
    @media (prefers-color-scheme: dark) {
        background-color: $primary-container-dm;
        a,p {
            color: $on-primary-container-dm;
        }
    }
}

.boxed-secondary {
    border-radius: 0.25em;
    padding: 10px;
    background-color: $secondary-container;
    p:last-child {
        margin-bottom: 0;
    }
    a,p {
        color:$on-secondary-container;
    }
    @media (prefers-color-scheme: dark) {
        background-color: $secondary-container-dm;
        a,p {
            color: $on-secondary-container-dm;
        }
    }
}

.boxed-tertiary {
    border-radius: 0.25em;
    padding: 10px;
    background-color: $tertiary-container;
    p:last-child {
        margin-bottom: 0;
    }
    a,p {
        color:$on-tertiary-container;
    }
    @media (prefers-color-scheme: dark) {
        background-color: $tertiary-container-dm;
        a,p {
            color: $on-tertiary-container-dm;
        }
    }
}