  %label {
    display: inline-block;
    padding: 0.16em 0.56em;
    margin-right: 0.5em;
    text-transform: uppercase;
    vertical-align: middle;
    border-radius: 0.2em;
    font-size: $h5;
    margin-bottom:0.2em;
  }

  .label-lecture {
    @extend %label;
    background-color: $primary-container;
    color: $on-primary-container;
    @media (prefers-color-scheme: dark) {
        background-color: $primary-container-dm;
        color: $on-primary-container-dm;
    }
  }

  .label-hw {
    @extend %label;
    background-color: $secondary-container;
    color: $on-secondary-container;
    @media (prefers-color-scheme: dark) {
        background-color: $secondary-container-dm;
        color: $on-secondary-container-dm;
    }
  }

  .label-exam {
    @extend %label;
    background-color: $tertiary-container;
    color: $on-tertiary-container;
    @media (prefers-color-scheme: dark) {
        background-color: $tertiary-container-dm;
        color: $on-tertiary-container-dm;
    }
  }

  .label-new {
    @extend %label;
    background-color: $secondary-container;
    color: $on-secondary-container;
    @media (prefers-color-scheme: dark) {
        background-color: $secondary-container-dm;
        color: $on-secondary-container-dm;
    }
  }

  .label-info {
    @extend %label;
    background-color: $tertiary-container;
    color: $on-tertiary-container;
    @media (prefers-color-scheme: dark) {
        background-color: $tertiary-container-dm;
        color: $on-tertiary-container-dm;
    }
  }