// Typography
$font-family: 'Montserrat';
$heading-font-family: 'Montserrat';
$monospace-font-family: monospace;

$base-font-size: 12px;
$base-font-viewport-small: 13px;
$base-font-viewport-medium: 14px;
$base-font-viewport-large: 14px;

$blog-font-family: 'Merriweather';
$drop-cap: false;

$bio-name-font-size: 2rem;

$line-height: 1.5;
$heading-line-height: 1.25;

$bold-font-weight: bold;
$heading-font-weight: bold;

$site-header-font-size: 1.5rem;
$h1: 1.5rem;
$h2: 1.2rem;
$h3: 1.1rem;
$h4: 1rem;
$h5: .875rem;
$h6: .75rem;

$abbr-border-bottom: 1px black dotted;

// Page width
$measure-width: 54rem;
$course-measure-width: 80rem;

// Sidenote width
$blog-width: 0.725;
$sidenote-width: 0.225;

// Viewport widths
$viewport-small: 34rem;
$viewport-medium: 52rem;
$viewport-large: 64rem;

// Colors
$surface: #fdfbff;
$surface-container: rgb(241, 240, 244);
$surface-container-highest: rgb(227, 226, 230);
$on-surface: #1a1c1e;
$outline: #74777f;
$primary: #215fa6;
$on-primary: #ffffff;
$primary-container: #d5e3ff;
$on-primary-container: #001c3b;
$secondary: #006c4e;
$on-secondary: #ffffff;
$secondary-container: #87f8c9;
$on-secondary-container: #002115;
$tertiary: #805600;
$on-tertiary: #ffffff;
$tertiary-container: #ffddaf;
$on-tertiary-container: #281800;

$surface-dm: #1a1c1e;
$surface-container-dm:rgb(26, 28, 30);
$surface-container-highest-dm: rgb(47, 48, 51);
$on-surface-dm: #e3e2e6;
$outline-dm: #8d9199;
$primary-dm: #a6c8ff;
$on-primary-dm: #003060;
$primary-container-dm: #004787;
$on-primary-container-dm: #d5e3ff;
$secondary-dm: #69dbae;
$on-secondary-dm: #003827;
$secondary-container-dm:#00513a;
$on-secondary-container-dm:#87f8c9;
$tertiary-dm: #ffba43;
$on-tertiary-dm: #442c00;
$tertiary-container-dm: #614000;
$on-tertiary-container-dm: #ffddaf;

// Whitespace
$space-1: .5rem;
$space-2: 1rem;
$space-3: 2rem;
$space-4: 4rem;

// Borders
$border-width: 2px;
$border-radius: 3px;
