button.link-button {
  background-color: transparent;
  border: 0;
  text-decoration: underline;
  padding: 0;
  cursor: pointer;
  color: $primary;
  @media (prefers-color-scheme: dark) {
    color: $primary-dm;
  }

  &:hover, &:active {
    text-decoration: underline;
    
  	color: $on-surface;
    @media (prefers-color-scheme: dark) {
      color: $on-surface-dm;
    }
  }
  &active, &:focus {
  	outline: 0;
  }
}